<template>
    <div class="form">
        <div class="title-page">模拟订单系统</div>

        <div class="title">选输入用户名</div>
        <div class="input">
            <Input v-model="name" placeholder="请输入用户名" style="width: 200px" />
        </div>

        <div class="title">选择商品</div>
        <div class="goos-list">
            <div class="goos-item"
                v-for="(item, index) in goods"
                :key="index"
                :class="type === item.type ? 'select': ''"
                @click="selectGood(item.type)">
            {{ item.title }}</div>
        </div>

        <div class="title">选择收货地址</div>
        <div class="goos-list">
            <div class="goos-item"
                v-for="(item,index) in areas"
                :key="index"
                :class="area === item.area ? 'select': ''"
                @click="selectArea(item.area)">
            {{ item.area }}</div>
        </div>


        <div class="btn" v-if="name && area && type" @click="submit">提交订单</div>
        <div class="btn disable" v-else>提交订单</div>

    </div>
</template>

<script>
import { ip } from './data'

export default {
    data: ()=>({
        inajax: false,
        name: '',
        type: null,
        area: null,
        goods: [
            {type: 1,title: 'AJ'},
            {type: 2,title: '衣服'},
            {type: 3,title: '手表'}
        ],
        areas: [
            {area: '安徽省铜陵'},
            {area: '长春'},
            {area: '长沙'},
            {area: '贵阳'},
            {area: '杨凌'},
            {area: '深圳'},
            {area: '济南'},
            {area: '海口'},
            {area: '沈阳'},
            {area: '武汉'},
            {area: '红安'},
            {area: '昆明'},
            {area: '杭州'},
            {area: '成都'},
            {area: '拉萨'},
            {area: '天津'},
            {area: '合肥'},
            {area: '呼和浩特'},
            {area: '哈尔滨'},
            {area: '北京'},
            {area: '南京'},
            {area: '南宁'},
            {area: '南昌'},
            {area: '乌鲁木齐'},
            {area: '上海'},
            {area: '三亚'},
            {area: '香港'},
        ]
    }),

    methods: {
        selectGood(type) {
            this.type = type
        },
        selectArea(area) {
            this.area = area
        },
        submit(){
            if(this.inajax) {
                return
            }

            const url = `http://${ip}:3000/buy`
            this.inajax = true

            fetch(`${url}?area=${this.area}&type=${this.type}&name=${this.name}`)
            .then((response)=> {
                this.$Message.info({
                    content: '提交成功',
                    duration: 10,
                });
                this.resetForm()
                this.inajax = false
                return response.json();
            })
            .then(function(myJson) {
                console.log(myJson);
                this.inajax = false
                this.$Message.error({
                    content: '服务器出小差了',
                    duration: 10,
                })
            });
        },
        resetForm(){
            this.name = ''
            this.type = null
            this.area = null
        }
    }

}
</script>
