<template>
  <!-- 中国地图展示 -->
  <div id="mapBox" class="centerItem"></div>
</template>

<script>
import { geoCoordMapD, icons } from "./data";
import { rd } from "../utils/methods.js";

export default {
  data() {
    return {
      geoCoordMap: geoCoordMapD,
      icons,
      map: null
    };
  },
  props: {
    mobile: Boolean,
  },
  computed: {
    list() {
      // {
      //     type: 0, // 鞋子 、 衣服 、 手表
      //     start: '北京', // 中文名
      //     end: '上海', // 中文名
      //     value: '', // 管理起始点的大小
      // }
      return this.$store.state.list;
    }
  },
  mounted() {
    this.$eb.$on("data", e => {
      let ele = {
        start: "上海",
        end: e.area,
        type: parseInt(e.type) - 1,
        value: "66",
        period: rd(3, 18),
        name: e.name
      };
      this.addlist(ele);
      this.updateMap();
    });
    // 基于准备好的 dom，初始化 echarts 实例
    this.map = echarts.init(document.getElementById("mapBox"));
    // this.$store.commit("updateMapData", this.generateList());
    this.updateMap();
    // this.dataLoop();
    this.map.on("click", params => {
      // console.log(params);
      this.$emit("clickProvince", params.name);
    });
  },
  methods: {
    addlist(e) {
      this.$store.commit("updateMapData", e);
    },
    // 随机制造数据
    generateList() {
      let orderNums = 1;
      let keys = Object.keys(geoCoordMapD);
      let list = [];
      for (let i = 0; i < orderNums; i++) {
        list.push({
          type: rd(0, 2),
          start: "上海", // keys[rd(0, keys.length - 1)],
          end: keys[rd(0, keys.length - 1)],
          value: "66",
          period: rd(3, 18),
          name: ["哈苏分", "哦儿哦", "风华国乐"][rd(0, 2)]
        });
      }
      return list[0];
    },
    dataLoop() {
      setInterval(() => {
        this.$store.commit("updateMapData", this.generateList());
        this.updateMap();
      }, 2000);
    },
    updateMap() {
      const _this = this;
      // 配置项
      var color = ["#00c1c2", "#FFFFA8", "#46bee9"]; // 圆圈和字的颜色，线的颜色，箭头颜色

      let series = [];
      // 路线配置
      this.list.forEach(e => {
        let item = [
          e.start,
          [[{ name: e.start }, { name: e.end, value: e.value }]]
        ];
        series.push(
          {
            // 路程中的线条
            name: item[0],
            type: "lines",
            zlevel: 1, // 用于 Canvas 分层，不同 zlevel 值的图形会放置在不同的 Canvas 中
            lineStyle: {
              normal: {
                color: color[0],
                width: 0,
                curveness: 0.3 // 幅度
              }
            },
            label: {
              normal: {
                show: true,
                // position: "right",
                color: "#eee",
                formatter: `${e.name}`,
                period: e.period / 2 // 特效动画的时间，单位为 s
              }
            },
            effect: {
              show: true,
              period: e.period, // 特效动画的时间，单位为 s
              trailLength: 0.5, // 特效尾迹的长度。取从 0 到 1 的值，数值越大尾迹越长
              color: "#46bee9", // 移动箭头颜色
              // formatter: `${e.name}`,
              symbol: "circle",
              symbolSize: 4 // 特效标记的大小
            },
            data: this.convertData(item[1])
          },
          {
            // 移动的物体
            name: item[0],
            type: "lines",
            zlevel: 2,
            lineStyle: {
              normal: {
                // background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
                color: new echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "#d4fc79" // 出发
                    },
                    {
                      offset: 1,
                      color: "#96e6a1" // 结束 颜色
                    }
                  ],
                  false
                ),
                width: 0.5,
                opacity: 0.4,
                curveness: 0.3
              }
            },
            effect: {
              show: true,
              period: e.period,
              trailLength: 0,
              symbol: this.icons[e.type],
              symbolSize: 18
            },
            data: this.convertData(item[1])
          },
          {
            // 目的地的样式
            name: item[0],
            type: "effectScatter",
            coordinateSystem: "geo",
            zlevel: 2,
            rippleEffect: {
              brushType: "stroke"
            },
            label: {
              normal: {
                show: true,
                position: "right",
                formatter: "{b}"
              }
            },
            symbolSize: function(val) {
              return val[2] / 8 / 2;
            },
            itemStyle: {
              normal: {
                color: color[0]
              }
            },
            data: item[1].map(dataItem => {
              return {
                name: dataItem[1].name,
                value: _this.geoCoordMap[dataItem[1].name].concat([
                  dataItem[1].value
                ])
              };
            })
          }
        );
      });

      // 地图配置
      var mapBoxOption = {
        geo: {
          map: "china",
          roam: !this.mobile, // 是否开启鼠标缩放和平移漫游。默认不开启。如果只想要开启缩放或者平移，可以设置成 'scale' 或者 'move'。设置成 true 为都开启
          aspectScale: 0.75,
          zoom: 1.2,
          label: {
            normal: {
              show: false,
              textStyle: {
                color: "#00a0c9"
              }
            },
            emphasis: {
              // 对应的鼠标悬浮效果
              show: false,
              textStyle: {
                color: "#00a0c9"
              }
            }
          },
          itemStyle: {
            normal: {
              areaColor: "#32466a", // 地图颜色
              borderColor: "#666", // 省分割线
              opacity: 0.75
            },
            emphasis: {
              borderWidth: 1,
              borderColor: "#333",
              areaColor: "#444",
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        },
        series: series
      };
      // 使用制定的配置项和数据显示图表
      this.map.setOption(mapBoxOption, true);
    },
    // push 进去线路开始 - 结束地点 - 经纬度
    convertData(data) {
      // [
      //      [{name: '上海'}, {name: '长春', value: 66}],
      // ]
      var res = [];
      for (var i = 0; i < data.length; i++) {
        var dataItem = data[i];
        var fromCoord = this.geoCoordMap[dataItem[0].name];
        var toCoord = this.geoCoordMap[dataItem[1].name];
        if (fromCoord && toCoord) {
          res.push([
            {
              coord: fromCoord
            },
            {
              coord: toCoord
            }
          ]);
        }
      }
      return res;
    }
  }
};
</script>

<style lang="scss" scoped>
#app {
  width: 100%;
  height: 100%;
}
.centerItem {
  /*position: absolute;*/
  width: 100%;
  height: 100%;
  /*background-color: rgba(0, 193, 194, 0.5);*/
  /* background-color: #2b333b; */
  margin: 0 auto;
}
